import { IDependencyContainer, ServiceToken, ServiceFactory, ServiceDescriptor, ServiceLifetime } from '../../../src/container/IDependencyContainer';
import { HealthHandler } from '../infrastructure/handlers/HealthHandler';
import { LogHandler } from '../infrastructure/handlers/LogHandler';
import { SdkHandler } from '../infrastructure/handlers/SdkHandler';
import { IRequestHandler } from '../domain/interfaces/IRequestHandler';

export class WorkerContainer implements IDependencyContainer {
  private services = new Map<string, ServiceDescriptor>();
  private instances = new Map<string, any>();

  register<T>(token: ServiceToken<T>, factory: ServiceFactory<T>): void {
    this.services.set(token.name, {
      token,
      factory,
      lifetime: ServiceLifetime.TRANSIENT
    });
  }

  registerSingleton<T>(token: ServiceToken<T>, factory: ServiceFactory<T>): void {
    this.services.set(token.name, {
      token,
      factory,
      lifetime: ServiceLifetime.SINGLETON
    });
  }

  registerInstance<T>(token: ServiceToken<T>, instance: T): void {
    this.instances.set(token.name, instance);
    this.services.set(token.name, {
      token,
      factory: () => instance,
      lifetime: ServiceLifetime.SINGLETON,
      instance
    });
  }

  resolve<T>(token: ServiceToken<T>): T {
    const descriptor = this.services.get(token.name);
    if (!descriptor) {
      throw new Error(`Service not registered: ${token.name}`);
    }

    if (descriptor.lifetime === ServiceLifetime.SINGLETON) {
      if (!descriptor.instance) {
        descriptor.instance = descriptor.factory(this);
      }
      return descriptor.instance;
    }

    return descriptor.factory(this);
  }

  isRegistered<T>(token: ServiceToken<T>): boolean {
    return this.services.has(token.name);
  }

  createScope(): IDependencyContainer {
    const scopedContainer = new WorkerContainer();
    
    for (const [key, descriptor] of this.services) {
      if (descriptor.lifetime === ServiceLifetime.SINGLETON) {
        scopedContainer.services.set(key, descriptor);
      } else {
        scopedContainer.services.set(key, {
          ...descriptor,
          lifetime: ServiceLifetime.SCOPED
        });
      }
    }
    
    return scopedContainer;
  }

  registerWorkerHandlers(): void {
    this.registerSingleton(
      { name: 'HealthHandler', type: 'HealthHandler' } as ServiceToken<HealthHandler>,
      () => new HealthHandler()
    );

    this.registerSingleton(
      { name: 'LogHandler', type: 'LogHandler' } as ServiceToken<LogHandler>,
      () => new LogHandler()
    );

    this.registerSingleton(
      { name: 'SdkHandler', type: 'SdkHandler' } as ServiceToken<SdkHandler>,
      () => new SdkHandler()
    );
  }

  getRequestHandlers(): IRequestHandler[] {
    return [
      this.resolve({ name: 'HealthHandler', type: 'HealthHandler' } as ServiceToken<HealthHandler>),
      this.resolve({ name: 'LogHandler', type: 'LogHandler' } as ServiceToken<LogHandler>),
      this.resolve({ name: 'SdkHandler', type: 'SdkHandler' } as ServiceToken<SdkHandler>)
    ];
  }
}